.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  background-color: rgb(19, 19, 46);
}
button {
  padding: 3px;
  margin: 2.5px;
  background-color: lightsalmon;
  border: none;
  border-style: solid;
  border-width: 1px;
  color: rgb(0, 14, 6);
  border-radius: 3px;
  width: 50px;
}
input {
  background-color: lightyellow;
  height: 20px;
  color: black;
}
li {
  list-style: none;
}
span {
  color: white;
  font-size: 25px;
  margin-right: 15px;
}
